body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
* {
  box-sizing: border-box;
}

/* reset */

a {
    text-decoration: none;
    color: inherit;
}
li {
    list-style-type: none;
}
ul {
    margin: 0;
    padding: 0;
}
/*normalize*/
a, button {
    display: inline-block;
    font-size: inherit;
    text-align: center;
}

button {
    font-family: inherit;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --main-purple: #4100fd;
  --secondary-purple: #8700fd;
}

.container {
  width: 60%;
  margin: 0 auto;
}

.firstInRow {
  padding-left: 0;
  list-style-type: disc;
}
.withSeparator {
  list-style-type: none;
}

.form {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 30px;
  background-color: #744d44;
}
.form__field,
.add-form__field,
.edit-form__field{
  display: block;
  padding: 7px 15px;
  margin: 5px 0;
  border: 2px inset paleturquoise;
  border-radius: 5px;
}
.add-form__field,
.edit-form__field {
    width: 100%;
}
.form__button, .home-page__button {
  display: block;
  padding: 7px 15px;
  margin: 5px 0;
  border: 2px outset paleturquoise;
  border-radius: 5px;
  background: repeating-linear-gradient(45deg, paleturquoise, transparent 240px);
  width: 300px;
}

.form__field {
width: 300px;
}

.main__link {
text-decoration: none;
color: inherit;
background-color: #744d44;
}
.bg, .home-page {
color: paleturquoise;
background-color: #744d44;
padding: 40px;
}
.dialog__overlay {
position: absolute;
top:0;
left:0;
width: 100%;
height: 100vh;
background-color: rgba(61, 61, 61, 0.49);
z-index: 1;
}
.dialog__window {
position: absolute;
top: 50%;
left: 50%;
width: 300px;
height: 300px;
padding: 15px;
background-color: #744d44;
transform: translate(-50%, -50%);
border-radius: 5px;
}

.dialog__msg {
color: paleturquoise;
}
.person__info-title {
font-style: italic;
margin-right: 10px;
}
.add-form__field {
    display: block;
}
.add-form,
.edit-form {
    width: 344px;
    padding: 20px;
    background: repeating-linear-gradient(
            45deg
            , #744d44, transparent 660px);
}

.name {
    transition: color 0.3s
}
.name:visited {
    color: purple;
}
.name:hover {
    color: turquoise;
}